<template>
<div id="App" :class="[deviceType]">
	<calendar-container
		ref="calendar"
		:db="db"
		:currentYYMM="{year: currentYear, month: currentMonth}"
		:changeMonthBtnFlg="changeMonthBtnFlg"
		:changeMonthFlg="changeMonthFlg"
		@ANsetCalendarData="setCalendarData"
		@ANsetChangeMonth="setChangeMonth"
		@ANswitchChangeMonth="switchChangeMonth"
		@ANopenPosts="openPosts"
		@ANopenAnewDcm="openAnewDcm"
		@ANopenAnewCtg="openAnewCtg"
		@ANswitchLoader="switchLoader">
	</calendar-container>
	<menu-container
		ref="menu"
		:db="db"
		:menuListBtnFlg="menuListBtnFlg"
		:menuListFlg="menuListFlg"
		@ANswitchMenuList="switchMenuList"
		@ANopenViewDcm="openViewDcm"
		@ANopenViewCtg="openViewCtg"
		@ANswitchLoader="switchLoader">
	</menu-container>
	<posts-container
		ref="posts"
		:db="db"
		:currentYYMM="{year: currentYear, month: currentMonth}"
		:postsFlg="postsFlg"
		@ANclosePosts="closePosts"
		@ANopenEditDcm="openEditDcm"
		@ANopenEditCtg="openEditCtg"
		@ANopenViewDcm="openViewDcm"
		@ANopenViewCtg="openViewCtg"
		@ANconfirmRem="confirmRem"
		@ANswitchLoader="switchLoader">
	</posts-container>
	<view-container
		ref="view"
		:db="db"
		:viewFlg="viewFlg"
		@ANopenViewDcm="openViewDcm"
		@ANopenEditDcm="openEditDcm"
		@ANopenEditCtg="openEditCtg"
		@ANconfirmRem="confirmRem"
		@ANcloseView="closeView"
		@ANswitchLoader="switchLoader">
	</view-container>
	<edit-container
		ref="edit"
		:db="db"
		:editFlg="editFlg"
		@ANcloseEditDcm="closeEditDcm"
		@ANcloseEditCtg="closeEditCtg"
		@ANswitchLoader="switchLoader">
	</edit-container>
	<anew-container
		ref="anew"
		:db="db"
		:anewFlg="anewFlg"
		@ANcloseAnew="closeAnew"
		@ANswitchLoader="switchLoader">
	</anew-container>
	<dialog-container
		v-if="dialogFlg"
		:btnClass="dialogBtnClass"
		@ANcloseDialog="closeDialog"
		@ANremoveItem="removeItem"
		@ANswitchLoader="switchLoader">
	</dialog-container>
	<loader-container v-if="loaderFlg"></loader-container>
</div>
</template>

<script>
import Vue from "vue"
import Dexie from "dexie"
import LoaderContainer from "./components/loader/LoaderContainer"
import DialogContainer from "./components/dialog/DialogContainer"
import CalendarContainer from "./components/calendar/CalendarContainer"
import MenuContainer from "./components/menu/MenuContainer"
import PostsContainer from "./components/posts/PostsContainer"
import ViewContainer from "./components/view/ViewContainer"
import EditContainer from "./components/edit/EditContainer"
import AnewContainer from "./components/anew/AnewContainer"

export default Vue.extend({
// AN Component
	name: "App",
	data() {
		return {
			loaderFlg: true,
			dialogFlg: false,
			dialogBtnClass: "",
			dbName: "netamemoDB",
			db: "",
			dbData: [],
			now: "",
			nowTime: 0,
			current: "",
			currentYear: 0,
			currentMonth: 0,
			currentDates: [],
			changeMonthList: {},
			changeMonthBtnFlg: false,
			changeMonthFlg: false,
			menuListBtnFlg: true,
			menuListFlg: false,
			markDate: 0,
			postsFlg: false,
			viewFlg: false,
			editFlg: false,
			currentField: "",
			anewFlg: false,
		}
	},
	components: {
		LoaderContainer,
		DialogContainer,
		CalendarContainer,
		MenuContainer,
		PostsContainer,
		ViewContainer,
		EditContainer,
		AnewContainer,
	},
	created: function(){
		this.checkDevice();
		this.setToday();
		this.createDB();
		this.createTable();
		this.markDate = this.now.getDate();
		// this.addDataDcm();
		// this.addDataCtg();
		// this.addDataTag();
		this.switchLoader();
	},
	methods: {
		checkDevice() {
			const ua = navigator.userAgent.toLowerCase();
			if(ua.indexOf("iphone")>0 || ua.indexOf("ipod")>0 || ua.indexOf("android")>0 && ua.indexOf("mobile")>0) {
				this.deviceType = "isTD";
			} else if (ua.indexOf("ipad")>0 || ua.indexOf("android")>0){
				this.deviceType = "isTD";
			} else {
				this.deviceType = "isMD";
			}
		},
		compileArrtoArr(arr) {
			let _arr = new Set(arr);
			return Array.from(_arr);
		},
		compileNltoBr(_str) {
			let str = String(_str);
			return str.replace(/<div>/, "\r\n").replace(/<\/div>/, "");
		},
		compileBrtoNl(_str) {
			let str = String(_str);
			return str.replace(/<br>/, "\r\n");
		},
		zeroPad(num, len) {
			return ('0000'+num).slice(-len);
		},
		getCurrentDates(y, m) {
			return new Date(parseInt(y, 10), parseInt(m+1, 10), 0).getDate();
		},
		sortASC(prop) {
			return function(a, b){
				if(a[prop]<b[prop]) return -1;
				if(a[prop]>b[prop]) return 1;
				return 0;
			};
		},
		sortDESC(prop) {
			return function(a, b){
				if(a[prop]>b[prop]) return -1;
				if(a[prop]<b[prop]) return 1;
				return 0;
			};
		},
		createDB() {
			this.db = new Dexie(this.dbName);
		},
		createTable() {
			this.db.version(1).stores({
				dcm: `++did, cid, tag, date, last, head, body`,
				ctg: `++cid, tag, date, color, head, body`,
				tag: `++gid, head`,
			});
			// did: Number, cid: Number, tag: Array, date: Date, last: Date, head: String, body: String
			// cid: Number, tag: Array, date: Date, color: #***, head: String, body: String
			// gid: Number, head: String
		},
		addDataDcm() {
			this.db.dcm.put({cid: 0, tag: [""], date: "2020/10/10", last: "2020/10/25", head: "", body: ""});
			this.db.dcm.put({cid: 1, tag: ["", ""], date: "2020/10/20", last: "2020/10/21", head: "", body: "<br>()()()()()()()()"});
			this.db.dcm.put({cid: 2, tag: [], date: "2020/10/30", last: "2020/10/30", head: "", body: "()()()"});
			this.db.dcm.put({cid: 2, tag: [""], date: "2020/10/31", last: "2020/10/31", head: "", body: "()()()"});
		},
		addDataCtg() {
			this.db.ctg.put({tag: ["", ""], date: "2020/10/22", color: "#fdcfdb", head: "", body: "()()()"});
			this.db.ctg.put({tag: [""], date: "2020/10/20", color: "#145c8a", head: "", body: "()()()()()()"});
		},
		addDataTag() {
			this.db.tag.put({head: ""});
			this.db.tag.put({head: ""});
			this.db.tag.put({head: ""});
		},
		switchLoader() {
			this.loaderFlg = this.loaderFlg ? false : true;
			console.log(this.loaderFlg);
		},
		setToday() {
			this.now = new Date();
			this.now = new Date(this.now.getFullYear(), this.now.getMonth(), this.now.getDate());
			this.nowTime = this.now.getTime();
			this.setCurrent({year: this.now.getFullYear(), month: this.now.getMonth()});
		},
		setCurrent(yymm) {
			this.current = new Date(yymm.year, yymm.month);
			this.currentYear = this.current.getFullYear();
			this.currentMonth = this.current.getMonth();
		},
		setCalendarData(arr) {
			this.currentDates = arr;
		},
		setChangeMonth(obj) {
			this.changeMonthList = Object.assign(obj);
			this.changeMonthBtnFlg = !Object.keys(this.changeMonthList).length ? false : true;
		},
		switchChangeMonth() {
			this.menuListBtnFlg = this.changeMonthFlg ? true : false;
			this.changeMonthFlg = this.changeMonthFlg ? false : true;
		},
		switchMenuList() {
			this.changeMonthBtnFlg = this.menuListFlg&&Object.keys(this.changeMonthList).length  ? true : false;
			this.menuListFlg = this.menuListFlg ? false : true;
		},
		openPosts(obj) {
			this.markDate = obj.date;
			this.$refs.posts.setPostsData(this.markDate);
			this.postsFlg = true;
			this.switchLoader();
		},
		closePosts() {
			this.postsFlg = false;
		},
		openViewDcm(id) {
			this.$refs.view.setDcmData(Number(id), true);
			this.viewFlg = true;
			this.switchLoader();
		},
		openViewCtg(id) {
			this.$refs.view.setCtgData(Number(id), true);
			this.viewFlg = true;
			this.switchLoader();
		},
		closeView() {
			this.viewFlg = this.viewFlg ? false : true;
		},
		openEditDcm(obj) {
			console.log(obj);
			this.$refs.edit.setEditDcmData(obj);
			this.editFlg = true;
			this.switchLoader();
		},
		openEditCtg(obj) {
			this.$refs.edit.setEditCtgData(obj);
			this.editFlg = true;
			this.switchLoader();
		},
		closeEditDcm(id) {
			this.editFlg = false;
			if(this.postsFlg) this.$refs.posts.setPostsData(this.markDate);
			if(id && this.viewFlg) this.$refs.view.setDcmData(Number(id), true);
			this.$refs.calendar.setCalendar();
			this.$refs.menu.setMenuData();
		},
		closeEditCtg(id) {
			this.editFlg = false;
			if(this.postsFlg) this.$refs.posts.setPostsData(this.markDate);
			if(id && this.viewFlg) this.$refs.view.setCtgData(Number(id), true);
			this.$refs.calendar.setCalendar();
			this.$refs.menu.setMenuData();
		},
		openAnewDcm() {
			this.$refs.anew.setAnew("dcm");
			this.anewFlg = true;
		},
		openAnewCtg() {
			this.$refs.anew.setAnew("ctg");
			this.anewFlg = true;
		},
		closeAnew() {
			this.anewFlg = false;
			this.$refs.calendar.setCalendar();
			this.$refs.menu.setMenuData();
		},
		switchDialog() {
			this.dialogFlg = this.dialogFlg ? false : true;
		},
		closeDialog() {
			const btn = document.getElementsByClassName("isRemove")[0];
			btn.classList.remove("isRemove");
			this.dialogBtnClass = "";
			this.switchDialog();
		},
		confirmRem(val) {
			this.switchDialog();
			this.dialogBtnClass = val;
		},
		async removeItem() {
			const btn = document.getElementsByClassName("isRemove")[0],
				item = btn.dataset.did ? "dcm" : "ctg";
			if(item=="dcm") {
				await this.removeDcm(Number(btn.dataset.did));
			} else {
				await this.setRemoveCtg(Number(btn.dataset.cid));
			}
			if(this.postsFlg) this.$refs.posts.setPostsData(this.markDate);
			if(this.viewFlg) this.closeView();
			this.switchDialog();
			this.$refs.calendar.setCalendar();
			this.$refs.menu.setMenuData();
			this.switchLoader();
		},
		removeDcm(id) {
			const that = this;
			return new Promise(function(resolve){
				that.db.dcm.delete(id).then(() => {
					resolve(true);
				});
			});
		},
		removeCtg(key) {
			const that = this;
			return new Promise(function(resolve){
				that.db.dcm.where(key).modify({cid: 0}).then(function(){
					resolve(true);
				});
			});
		},
		async setRemoveCtg(id) {
			const that = this;
			await that.removeCtg({cid: id});
			return new Promise(function(resolve){
				that.db.ctg.delete(id).then(() => {
					resolve(true);
				});
			});
		},
	},
	computed: {
	},
});
</script>

<style>
@import "./css/app.css";
</style>
