<template>
<ul v-if="markDcmArr.length" class="list">
	<li v-for="(mt, mtidx) in markDcmArr" :key="mtidx">
		<div class="cardItem">
			<i v-if="mt.cid" :style="{color: mt.color}"></i>
			<h2>{{ mt.head }}</h2>
			<time class="date">{{ new Date(mt.date).getFullYear() }}{{ new Date(mt.date).getMonth()+1 }}{{ new Date(mt.date).getDate() }}</time>
			<time class="last">{{ new Date(mt.last).getFullYear() }}{{ new Date(mt.last).getMonth()+1 }}{{ new Date(mt.last).getDate() }}</time>
			<p :data-did="mt.did">
				<button type="button" class="btnIcon def dcm edt" @click="callopenEditDcm"><span></span></button>
				<button type="button" class="btnIcon def dcm viw" @click="callopenViewDcm"><span></span></button>
				<button type="button" class="btnIcon def dcm rem" @click="callconfirmRem"><span></span></button>
			</p>
		</div>
	</li>
</ul>
</template>

<script>
export default {
// PT Component
	name: "PostsText",
	props: {
        markDcmArr: Array,
	},
	methods: {
		callopenEditDcm(e) {
			const btn = e.target,
				id = Number(btn.parentNode.dataset.did);
			this.$emit("GPCallswitchLoader");
			this.$parent.openEditDcm(id);
		},
		callopenViewDcm(e) {
			const btn = e.target,
				id = Number(btn.parentNode.dataset.did);
			this.$emit("GPCallswitchLoader");
			this.$emit("GPCallopenViewDcm", id);
		},
		callconfirmRem(e) {
			const btn = e.target;
			btn.parentNode.classList.add("isRemove");
			this.$emit("GPCallconfirmRem", "dcm");
		},
	},
}
</script>